@font-face {
    font-family: 'G';
    src: url('./fonts/Geometria.otf'); }
@font-face {
    font-family: 'GB';
    src: url('./fonts/Geometria-Bold.otf'); }

$main: #631AEE;

* {
    box-sizing: border-box; }

body {
    background: #f3f3f3 url(./img/bg.jpg) no-repeat center / cover;
    font-family: G;
    width: 100%;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    padding-top: 100px; }
input,
select,
label,
textarea,
button {
    font-family: G; }

.part {
    margin: auto;
    max-width: 1200px;
    display: flex; }

.table {}

.col-wrapper {
    margin: 5px;
    &[width="100"] {
        flex-basis: 100%; }
    &[width="50"] {
        flex-basis: 50%; } }
.th {
    text-align: center;
    background-color: $main;
    border-radius: 10px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-family: GB;
    span {
        width: 100%; }
    .options {
        display: flex;
        justify-content: center;
        padding: 5px;
        font-size: 22px;
        position: relative;
        min-width: 45px;
        .menu {
            position: absolute;
            top: calc(100% + 5px);
            right: 0;
            background: #f3f3f3;
            color: #333;
            z-index: 1;
            font-size: 14px;
            min-width: 200px;
            padding: 15px;
            border: 1px solid #dddddd;
            border-radius: 8px;
            display: none;
            text-align: left;
            white-space: nowrap;
            .menu-item {
                margin: 12px 0;
                cursor: pointer;
                display: flex;
                align-items: center;
                i {
                    margin-right: 10px;
                    font-size: 1.1rem; }
                &:hover span {
                    text-decoration: underline; } } }
        &.open .menu {
            display: block; }
        i {
            cursor: pointer;
            opacity: .85;
            transition: all .3s;
            &:hover {
                opacity: 1; } } } }
.cell-wrapper {
    display: flex;
    align-items: center;
    label {
        width: 100%;
        position: relative; }
    &[disabled] {
        label,i {
            opacity: .4; } }
    &:nth-child(2) {
        margin-top: 10px; }
    & i {
        font-size: 20px;
        cursor: pointer; } }

i.bi-x {
    color: red;
    font-weight: bolder; }
i.bi {
    color: $main; }

.cell-input {
    padding: 10px 15px;
    border: 1px solid #ddd;
    margin: 3px 0;
    border-radius: 5px;
    background-color: #fff;
    font-size: 0.8rem;
    width: 100%; }

.selected-marker {
    position: absolute;
    right: -2px;
    bottom: 1px;
    width: 8px;
    height: 8px;
    background-color: #000;
    cursor: pointer; }

.table {
    display: flex;
    justify-content: center; }

.before {
    i {
        transform: all 0.3s;
        margin: 0 5px;
        color: $main;
        &:hover {
            color: $main; } } }

.sidebar {
    width: 130px;
    padding: 15px;
    .btn {
        border: 1px outset #ddd;
        text-transform: uppercase;
        font-size: 10px;
        padding: 10px 5px;
        text-align: center;
        background-color: #fff;
        cursor: pointer;
        border-radius: 3px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all .3s;
        &:hover {
            background-color: #f9f9f9; }
        &.changed {
            background-color: $main;
            color: #fff; }
        .badge {
            border: 1px solid #333;
            min-width: 17px;
            width: fit-content;
            display: block;
            border-radius: 50px;
            padding: 1px 3px;
            position: absolute;
            bottom: -10px;
            left: -7px;
            background-color: #fff; }
        &.icon {
            font-size: 2rem;
            line-height: 1;
            height: 45px; } }
    .delimeter {
        border-top: 2px solid #999;
        margin: 15px auto;
        width: 80%; }
    .widget {
        position: relative;
        margin-bottom: 15px;
        .sidebar-submenu {
            background-color: #f3f3f3;
            border: 1px solid #ddd;
            border-radius: 3px;
            min-width: 40px;
            padding: 5px;
            position: absolute;
            right: 105%;
            top: 0;
            display: flex;
            align-items: center;
            flex-direction: column; } } }

.faq {
    margin-top: 40px;
    display: block;
    padding: 0;
    font-size: 0.9rem;
    li {
        margin: 7px; } }
